"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  ScrollText,
  Search,
  Filter,
  Download,
  AlertTriangle,
  Info,
  CheckCircle,
  XCircle,
  Shield,
  Clock,
  RefreshCw,
  Trash2
} from "lucide-react"

interface LogEntry {
  id: string
  timestamp: Date
  level: 'info' | 'warn' | 'error' | 'debug'
  category: 'system' | 'player' | 'world' | 'security' | 'performance'
  message: string
  details?: string
  player?: string
}

const generateMockLogs = (): LogEntry[] => {
  const logs: LogEntry[] = []
  const now = new Date()
  const categories = ['system', 'player', 'world', 'security', 'performance']
  const levels = ['info', 'warn', 'error', 'debug']
  
  const messages = {
    system: [
      "Servidor iniciado com sucesso",
      "Backup automático iniciado",
      "Atualizando configurações",
      "Reiniciando servidor",
    ],
    player: [
      "Jogador entrou no servidor",
      "Jogador saiu do servidor",
      "Jogador mudou de mundo",
      "Jogador morreu",
    ],
    world: [
      "Chunk corrompido detectado",
      "Gerando novo terreno",
      "Salvando mundo",
      "Carregando estruturas",
    ],
    security: [
      "Tentativa de login suspeita",
      "IP banido detectado",
      "Modificação suspeita detectada",
      "Comando administrativo executado",
    ],
    performance: [
      "TPS baixo detectado",
      "Uso de memória alto",
      "Lag spike detectado",
      "Otimizando chunks",
    ],
  }

  for (let i = 0; i < 100; i++) {
    const category = categories[Math.floor(Math.random() * categories.length)] as keyof typeof messages
    const level = levels[Math.floor(Math.random() * levels.length)] as 'info' | 'warn' | 'error' | 'debug'
    const message = messages[category][Math.floor(Math.random() * messages[category].length)]
    
    logs.push({
      id: `log-${i}`,
      timestamp: new Date(now.getTime() - Math.random() * 86400000),
      level,
      category: category as 'system' | 'player' | 'world' | 'security' | 'performance',
      message,
      details: Math.random() > 0.7 ? "Detalhes adicionais do log..." : undefined,
      player: category === 'player' ? ['Steve', 'Alex', 'Notch'][Math.floor(Math.random() * 3)] : undefined,
    })
  }

  return logs.sort((a, b) => b.timestamp.getTime() - a.timestamp.getTime())
}

export default function LogsPage() {
  const [logs, setLogs] = useState<LogEntry[]>(generateMockLogs())
  const [searchTerm, setSearchTerm] = useState("")
  const [levelFilter, setLevelFilter] = useState<string>("all")
  const [categoryFilter, setCategoryFilter] = useState<string>("all")
  const [autoRefresh, setAutoRefresh] = useState(true)

  useEffect(() => {
    if (!autoRefresh) return

    const interval = setInterval(() => {
      const newLog: LogEntry = {
        id: `log-${Date.now()}`,
        timestamp: new Date(),
        level: ['info', 'warn', 'error', 'debug'][Math.floor(Math.random() * 4)] as 'info' | 'warn' | 'error' | 'debug',
        category: ['system', 'player', 'world', 'security', 'performance'][Math.floor(Math.random() * 5)] as 'system' | 'player' | 'world' | 'security' | 'performance',
        message: "Novo evento do servidor...",
      }
      setLogs(prev => [newLog, ...prev])
    }, 5000)

    return () => clearInterval(interval)
  }, [autoRefresh])

  const filteredLogs = logs.filter(log => {
    const matchesSearch = 
      log.message.toLowerCase().includes(searchTerm.toLowerCase()) ||
      (log.player?.toLowerCase().includes(searchTerm.toLowerCase())) ||
      (log.details?.toLowerCase().includes(searchTerm.toLowerCase()))
    
    const matchesLevel = levelFilter === "all" || log.level === levelFilter
    const matchesCategory = categoryFilter === "all" || log.category === categoryFilter

    return matchesSearch && matchesLevel && matchesCategory
  })

  const getLevelIcon = (level: string) => {
    switch (level) {
      case 'info': return <Info className="h-4 w-4 text-blue-400" />
      case 'warn': return <AlertTriangle className="h-4 w-4 text-yellow-400" />
      case 'error': return <XCircle className="h-4 w-4 text-red-400" />
      case 'debug': return <CheckCircle className="h-4 w-4 text-green-400" />
      default: return null
    }
  }

  const getLevelColor = (level: string) => {
    switch (level) {
      case 'info': return 'bg-blue-600'
      case 'warn': return 'bg-yellow-600'
      case 'error': return 'bg-red-600'
      case 'debug': return 'bg-green-600'
      default: return 'bg-gray-600'
    }
  }

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'system': return 'bg-purple-600'
      case 'player': return 'bg-blue-600'
      case 'world': return 'bg-green-600'
      case 'security': return 'bg-red-600'
      case 'performance': return 'bg-yellow-600'
      default: return 'bg-gray-600'
    }
  }

  const formatTimestamp = (date: Date) => {
    return date.toLocaleString('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit'
    })
  }

  const handleClearLogs = () => {
    if (window.confirm('Tem certeza que deseja limpar todos os logs?')) {
      setLogs([])
    }
  }

  const handleDownloadLogs = () => {
    const logsText = filteredLogs
      .map(log => `[${formatTimestamp(log.timestamp)}] [${log.level.toUpperCase()}] [${log.category}] ${log.message}${log.details ? ` - ${log.details}` : ''}`)
      .join('\n')
    
    const blob = new Blob([logsText], { type: 'text/plain' })
    const url = URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = `server-logs-${new Date().toISOString().split('T')[0]}.txt`
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
    URL.revokeObjectURL(url)
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-white">Logs do Servidor</h1>
          <p className="text-gray-400">Monitore e analise os logs do servidor</p>
        </div>
        <div className="flex items-center space-x-2">
          <Button
            variant="outline"
            className={`border-blue-600 ${autoRefresh ? 'text-blue-400' : 'text-gray-400'}`}
            onClick={() => setAutoRefresh(!autoRefresh)}
          >
            <RefreshCw className={`mr-2 h-4 w-4 ${autoRefresh ? 'animate-spin' : ''}`} />
            {autoRefresh ? 'Auto Refresh' : 'Refresh Off'}
          </Button>
          <Button
            variant="outline"
            className="border-red-600 text-red-400"
            onClick={handleClearLogs}
          >
            <Trash2 className="mr-2 h-4 w-4" />
            Limpar Logs
          </Button>
          <Button
            variant="outline"
            className="border-green-600 text-green-400"
            onClick={handleDownloadLogs}
          >
            <Download className="mr-2 h-4 w-4" />
            Download Logs
          </Button>
        </div>
      </div>

      {/* Filters */}
      <Card className="bg-gray-800 border-gray-700">
        <CardContent className="p-4">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="relative col-span-2">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <Input
                placeholder="Buscar nos logs..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 bg-gray-700 border-gray-600"
              />
            </div>

            <Select value={levelFilter} onValueChange={setLevelFilter}>
              <SelectTrigger className="bg-gray-700 border-gray-600">
                <SelectValue placeholder="Nível" />
              </SelectTrigger>
              <SelectContent className="bg-gray-700 border-gray-600">
                <SelectItem value="all">Todos os níveis</SelectItem>
                <SelectItem value="info">Info</SelectItem>
                <SelectItem value="warn">Warn</SelectItem>
                <SelectItem value="error">Error</SelectItem>
                <SelectItem value="debug">Debug</SelectItem>
              </SelectContent>
            </Select>

            <Select value={categoryFilter} onValueChange={setCategoryFilter}>
              <SelectTrigger className="bg-gray-700 border-gray-600">
                <SelectValue placeholder="Categoria" />
              </SelectTrigger>
              <SelectContent className="bg-gray-700 border-gray-600">
                <SelectItem value="all">Todas as categorias</SelectItem>
                <SelectItem value="system">Sistema</SelectItem>
                <SelectItem value="player">Jogadores</SelectItem>
                <SelectItem value="world">Mundo</SelectItem>
                <SelectItem value="security">Segurança</SelectItem>
                <SelectItem value="performance">Performance</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </CardContent>
      </Card>

      {/* Logs */}
      <Card className="bg-gray-800 border-gray-700">
        <CardHeader className="pb-3">
          <div className="flex items-center justify-between">
            <CardTitle className="text-white flex items-center">
              <ScrollText className="mr-2 h-5 w-5" />
              Logs em Tempo Real
            </CardTitle>
            <Badge variant="secondary">
              {filteredLogs.length} registros
            </Badge>
          </div>
        </CardHeader>
        <CardContent className="p-0">
          <ScrollArea className="h-[600px] p-4">
            <div className="space-y-2">
              {filteredLogs.map((log) => (
                <div
                  key={log.id}
                  className="p-3 rounded bg-gray-700/50 hover:bg-gray-700/70 transition-colors"
                >
                  <div className="flex items-start justify-between">
                    <div className="flex items-start space-x-3">
                      <div className="mt-1">{getLevelIcon(log.level)}</div>
                      <div>
                        <div className="flex items-center space-x-2">
                          <Badge className={getLevelColor(log.level)}>
                            {log.level.toUpperCase()}
                          </Badge>
                          <Badge className={getCategoryColor(log.category)}>
                            {log.category}
                          </Badge>
                          {log.player && (
                            <Badge variant="outline">
                              {log.player}
                            </Badge>
                          )}
                        </div>
                        <p className="text-white mt-1">{log.message}</p>
                        {log.details && (
                          <p className="text-gray-400 text-sm mt-1">{log.details}</p>
                        )}
                      </div>
                    </div>
                    <span className="text-xs text-gray-400 whitespace-nowrap ml-4">
                      {formatTimestamp(log.timestamp)}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </ScrollArea>
        </CardContent>
      </Card>
    </div>
  )
}
